<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <ModalView
      :title="`View Attendee Details`"
      :item="selected"
    />

    <b-container v-if="!isLoading" fluid>
      <b-alert v-if="!isAdmin" variant="info" show>Sign in to view this page.</b-alert>
      <b-row>
        <b-table
          table-variant="info"
          align="left"
          :items="[ceremony]"
          :fields="ceremonyFields"
        >
          <template #cell(scheduled_datetime)="row">
            {{ row.item.scheduled_datetime.toLocaleString() }}
          </template>

        </b-table>
      </b-row>
      <b-row align-h="between">
        <b-col cols="6">
          <b-form-group
            label="Status"
            label-for="table-select-filter-category"
            label-cols="3"
          >
            <b-form-select
              id="table-select-filter-category"
              v-model="selectCategory"
              :options="categories || []"
              class="mb-3"
              :disabled="this.processing"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col align="right">
          <b-spinner class="m-1" small v-if="this.processing" />
          <b-button class="m-1" size="sm" @click="clearFilters">Clear Filters</b-button>
          <b-button
            size="sm"
            class="m-1"
            variant="dark"
            :disabled="this.processing"
            @click="() => {this.loadData()}"
          >
            <BIconArrowRepeat /> Refresh
          </b-button>
          <b-button
            size="sm"
            variant="dark"
            class="m-1" @click="reroute(`/ceremonies`)">
            Manage Ceremonies
          </b-button>
        </b-col>
      </b-row>
    </b-container>

    <b-table
      :items="recipients || []"
      :fields="fields"
      striped
      :filter="criteria"
      :filter-function="applyFilter"
      :sort-by="sortBy"
      :sort-desc="sortDesc"
      responsive="sm"
      :busy.sync="loading"
    >

      <template #cell(status)="row">
        <b-badge class="m-1" :variant="getStatus(row.item.status)">
          {{ lookup('assignments', row.item.status) }}
        </b-badge>
      </template>

      <template #cell(guest)="row">
        {{row.item.guest ? 'Yes' : 'No'}}
      </template>

      <template #cell(details)="row">
        <b-button
          size="sm"
          class="m-1"
          v-b-modal.modal-view-item
          @click="selectItem(row.item)"
        >
          View
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          @click="reroute(`/attendees/edit/${row.item.id}`)"
        >
          Edit
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          @click="reroute(`/recipients/view/${row.item.recipient_id}`)"
        >
          Recipient Record
        </b-button>
      </template>

    </b-table>

    <b-alert v-if="error" show variant="danger">
      A server error occurred. Please contact the site administrator for assistance.
    </b-alert>

  </b-container>
</template>

<script>

import PageHeader from '@/components/PageHeader'
import reportServices from "@/services/reports.services";
import {get} from "@/services/api.services";
import optionsServices from '@/services/options.services'
import ModalView from '@/components/ModalView'
import { BIconArrowRepeat } from 'bootstrap-vue'


export default {
  name: 'reports-list',
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader, ModalView, BIconArrowRepeat
  },
  data () {
    return {
      loading: false,
      error: false,
      message: '',
      sortBy: 'name',
      sortDesc: true,
      selectCategory: '',
      categories: optionsServices.get('assignments'),
      selected: '',
      fields: [
        {key: 'status', label: 'Status', sortable: true},
        {key: 'first_name', label: 'First Name', sortable: true},
        {key: 'last_name', label: 'Last Name', sortable: true},
        {key: 'guest', label: 'With Guest', sortable: true},
        {key: 'details', label: '', sortable: false}
      ],
      ceremonyFields: [
        {key: 'location', label: 'Location'},
        {key: 'scheduled_datetime', label: 'Date/Time'},
        {key: 'total_recipients', label: 'Recipients'},
        {key: 'total_guests', label: 'Guests'},
        {key: 'total_attendees', label: 'Attendees'},
      ],
      ceremony: null,
      recipients: [],
      guests: [],
      totals: {
        total_recipients: 0,
        total_guests: 0,
        total_vips: 0,
        total_attendees: 0
      }
    }
  },
  computed: {
    isLoading () {
      return this.$store.getters.isLoading
    },
    isOrgContact () {
      return this.$store.getters.isOrgContact
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    reports () {
      return reportServices.reports() || []
    },
    criteria () {
      return {
        category: this.selectCategory
      }
    },
  },
  methods: {
    clearFilters() {
      this.selectCategory = ''
    },
    applyFilter(row) {
      return !this.criteria.category || row.status === this.criteria.category
    },
    selectItem(item) {
      // filter attendee data for viewing
      this.selected = {
        first_name: item.first_name,
        last_name: item.last_name,
        employee_number: item.employee_number,
        status: this.lookup('assignments', item.status),
        options: (item.accommodations || []).map(accommodation => {
          return `${accommodation.full_name} (${accommodation.type})`
        }).join('; '),
        has_guest: item.guest ? 'Yes' : 'No',
        guest_options: this.guests.filter(guest => {return guest.attendable_id === item.guest}).map(guest => {
          return (guest.accommodations || {}).map(accommodation => {
            return `${accommodation.full_name} (${accommodation.type})`
          }).join('; ')
        }).join()
      }
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    async loadData () {
      this.processing = true
      this.error = false

      // get ceremony data
      const [ceremonyError, ceremony] = await get(`ceremonies/show/${this.$route.params.id || ''}`)
      if (ceremonyError) {
        this.error = true
        this.$store.commit('setMessage', ceremonyError)
      }
      this.ceremony = ceremony

      // get attendees data
      const [error, attendees] = await get(`attendees/list/${this.$route.params.id || ''}`)
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }
      const {
        guests=[],
        recipients=[],
        total_guests=0,
        total_recipients=0,
        total_attendees=0,
        total_vips=0
      } = attendees || {}
      this.recipients = recipients
      this.guests = guests

      // update ceremony counts
      this.ceremony.total_attendees = total_attendees
      this.ceremony.total_guests = total_guests
      this.ceremony.total_recipients = total_recipients
      this.ceremony.total_vips = total_vips

      console.log(this.ceremony)
      console.log(this.recipients)
      console.log(this.guests)
      this.processing = false
    },
    lookup(key, value) {
      return optionsServices.lookup(key, value)
    },
    getStatus(status) {
      const variants = {
        assigned: 'info',
        waitlisted: 'warning',
        invited: 'dark',
        attending: 'success',
        declined: 'danger'
      }
      return variants[status] || 'secondary'
    },
  },
  async beforeMount () {
    this.$store.commit('setLoading', true)
    // retrieve attendees for this ceremony
    await this.loadData()
    this.$store.commit('setLoading', false)
  }
}
</script>
